.shadow{
  /* box-shadow: 0px 1px 10px rgba(90, 89, 89, 0.5); */
  border-bottom: 10px solid #55efc4;
}

.dark .shadow{
  /* box-shadow: 0px 0px 0px rgba(90, 89, 89, 0.5); */
  border-bottom: 10px solid #24282a;
}
.dark .nav-header{
  background-color: #1c2022;
}
.dark .darkmode-btn{
  color: #fff;
}
.dark .search-body{
  background-color: #24282a;
}
.dark .content{
  background-color: #1c2022;
}
.dark .book{
  background-color: #24282a;
  color: #fff;
}
.dark{
  background-color: #1c2022;
}

.dark .book .title-link{
  color: #dadada;
}
.dark .book .text-grey{
  color: #fff;
}

.dark .title-surah{
  color: #dadada;
}
.dark .item{
  color: #fff;
}
.dark .arabic{
  color: #dadada;
}
.dark .translate{
  color: #dadada;
}
.dark .bx-items .item{
  background-color: #24282a;
}
.dark .bottom-player{
  background-color: #1c2022;
  border-radius: 8px;
}
.dark .basic-background{
  position: fixed;
  min-width: 100%;
  min-height: 100%;
  background-color: #1c2022;
  z-index: -1;
}


.nav-header{
  background-color: #00b894;
  padding-top: .5rem;
  padding-bottom: .5rem;
}
.back{
  float: left;
  padding: 0 0 0 30px;
}
.link-back{
  color: #676767;
  text-decoration: none;
}.link-back:hover{
  color: #55efc4;
}

.right-choice-surah{
  float: right;
  padding: 0 30px 0 0;
}

.t-center{
  text-align: center;
}

.brand{
  color: #fff;
  font-weight: 700;
  font-size: 30px;
}

.search-body{
  background-color: #55efc4;
  padding: 3rem 0 3rem 0;
  display: flex;
  flex-direction: column;
}

.bs-form-input{
  display: block;
  width: 50%;
  left: 0;
  right: 0;
  padding: .8rem;
  border:none;
  margin: auto;
  font-size: 20px;
}

.bs-btn{
  border:none;
  border-radius: 0;
  padding: .8rem 1.4rem;
  font-size: 20px;
  color: #fff;
  background-color: #00b894;
  cursor: pointer;
}

.box{
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  margin-top: 20px;
}
.box.box-center{
  justify-content: center;
}

.box > .items{
  text-align: center;
  padding: .5rem;
  width: 200px;
}

.book{
  border:1px solid #ccc;
  border-radius: 10px;
  /* box-shadow: 0 1px 5px 0 #ccc; */
  padding: 2px;
}
.number-home{
  position: absolute;
  border: none;
  border-radius: 50px;
  padding: 10px;
  margin-top: -14px;
  margin-left: -35px;
}

.title-link{
  text-decoration: none;
  color: #676767;
  font-weight: 700;
  font-size: 30px;
}
.text-grey{
  color: #676767;
}

.content{
  text-align: center;
  /* background-color: #ccc; */
}
.bx-items{
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-bottom: 80px;
}
@media(min-width: 920px){
  .bx-items{
    display: flex;
    flex-direction: column;
    margin: 0 300px;
    margin-bottom: 80px;
  }
}
@media(max-width: 768px){
  .bx-items{
    display: flex;
    flex-direction: column;
    margin: 0;
    margin-bottom: 80px;
  }
}
.bx-items > .item{
  padding: 30px 20px;
  margin-top: 5px;
  margin-bottom: 5px;
  border-radius: 8px;
  right: 0;
  text-align: right;
}

.arabic{
  display: block;  
  font-size: 34px;
  color: #676767;
}
.arabic-tr{
  display: block;
  font-size: 14px;
  margin-top: 20px;
  /* color: #ccc; */
}
.translate{
  margin-top: 20px;
  display: block;
  text-align: left;
  color: #676767;
}
.nomor{
  color: #fff;
  font-weight: 700;
  font-size: 20px;
  margin-top: 5px;
  margin-bottom: 10px;
  text-align: center;
  display: block;
  border-radius: 50px;
}.nomor::after{
  content: '';
  position: absolute;
  display: inline-block;
  width: 80px;
  height: 30px;
  background-color: #00b894;
  margin: 0 0 0 -52px;
  z-index: -1;
  border-radius: 50px;
}

._404{
  width: 50%;
  margin: auto;
  text-align: center;
  padding: 2rem;
}

._404desk{
  display: block;
  margin-top: 50px;
  margin-bottom: 20px;
  font-size: 20px;
}

._404home{
  text-decoration: none;
  color: #00b894;
}
._404home:hover{
  color: #55efc4;
}

.goUp{
  position: fixed;
  right: 0;
  bottom: 0;
  cursor: pointer;
  font-weight: 700;
  font-size: 30px;
  margin-right: 40px;
  margin-bottom: 40px;
  line-height: 0;
  color: #fff;
}.goUp::after{
  content: '';
  position: absolute;
  width: 50px;
  height: 50px;
  background-color: #00b894;
  right: 0;
  bottom: 0;
  margin-right: -15px;
  margin-bottom: -22px;
  z-index: -1;
  border-radius: 50px;
}

.bottom-player{
  position: fixed;
  bottom: 0;
  left: 0;
  min-width: 15%;
  background-color: #fff;
  padding: 20px 20px;
  text-align: left;
}

.right-navbar{
  float: right;
}

.darkmode-btn{
  padding-right: 20px;
  padding-top: 5px;
  font-size: 20px;
  font-weight: 700;
  cursor: pointer;
}